import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate, useHistory } from 'react-router-dom';
import Select, { createFilter } from 'react-select';
import Modelbox from '../ModelBox/ModelBox';
import "./Selectbox.css"
import { useDispatch, useSelector } from 'react-redux';
import {changeMarka, changeModel, changeCity, changeMinYear, changeMaxYear, changeBan, changeColor, changeOwnerType}  from '../../features/search';


function Selectbox({type, options, title}) {



  









  const search = useSelector((state) => state.search.value);
  const hyundai = [
    { value: 'santafe', label: 'Santa-Fe' },
    { value: 'sonata', label: 'Sonata' },
    { value: 'quanji', label: 'Quanji' },
  ];
  const landrover = [
    { value: 'rangerover', label: 'Range Rover' },
    { value: 'velar', label: 'Velar' },
    { value: 'rangeroversport', label: 'Range Rover Sport' },
  ];


  const optionsMap = {
    hyundai: hyundai,
    // Add options for other types:
    landrover: landrover
    // ...
  };





  
  const dispatch = useDispatch();







    const styles = {
  option: (provided, state) => ({
    ...provided,
    textAlign: 'left', // Align options to the left
    fontFamily: "Inter",
  }),
  control: (provided) => ({
    ...provided,
    textAlign: 'left',
    borderRadius: '12px',
    fontFamily: "Inter",
    fontWeight: "500",
    height: "45px",
  }),
  
  
  menu: (provided, state) => ({
    ...provided,
    borderRadius: '10px', // Set your desired border radius here
    marginTop: 0,
    paddingBottom: "5px",
  }),
};
const filterOption = createFilter({
    matchFrom: 'start', // Match from the beginning of the label
    allowEmpty: true, // Allow empty search string
    trim: false, // Don't trim whitespace from input
    filterOption: (option, inputValue) =>
      option.label.toLowerCase().startsWith(inputValue.toLowerCase()),
  });
  const [iValue, setIvalue] = useState("all");
  const navigate = useNavigate();
  useEffect(() => {
    if (iValue == "all") {
      dispatch(changeMarka("all") );
      dispatch(changeModel("all") );
      dispatch(changeCity("all") );
      dispatch(changeBan("all") );
      dispatch(changeColor("all") );
    }
    
    
  }, [iValue])
  
  function handleChange(newValue){
    // update query parameters
    setIvalue(newValue.value);
    
    switch (type) {
      case "marka":
        dispatch(changeMarka(newValue.value) );
        break;
      case "model":
        dispatch(changeModel(newValue.value) );
        break;
      case "city":
        dispatch(changeCity(newValue.value) );
        break;
      case "ban":
        dispatch(changeBan(newValue.value) );
        break;
       case "color":
        dispatch(changeColor(newValue.value) );
        break; 
      
    }
    
    
    
  }

  


  // Example usage




  return (
    <div className='selectbox-container'>
      <div className='selectbox'  style={{width: "100%"}}
      >
        <Select
            
            className="basic-single"
            classNamePrefix="select"
            defaultValue={null}
            filterOption={filterOption}
            isClearable={false}
            isSearchable={true}
            name="color"
            options={(type == "model") ? optionsMap[search.marka] : options}
            onChange={handleChange}
            styles={styles}
            placeholder={title}
            components={{
              IndicatorSeparator: () => null
            }}
            
          />
      </div>
    
    </div>
    
  );
};

export default Selectbox;
